  --piko-font-family-sans-serif: system-ui, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, var(--piko-font-family-emoji);
  --piko-font-family-monospace: ui-monospace, SFMono-Regular, "SF Mono", Menlo, Consolas, "Liberation Mono", monospace, var(--piko-font-family-emoji);
  --piko-font-family: var(--piko-font-family-sans-serif);
  --piko-line-height: 1.5;
  --piko-font-weight: 400;
  --piko-font-size: 100%;
  --piko-text-underline-offset: 0.1rem;
  --piko-border-radius: 0.25rem;
  --piko-border-width: 0.0625rem;
  --piko-outline-width: 0.1875rem;
  --piko-transition: 0.2s ease-in-out;
  --piko-spacing: 1rem;
  --piko-typography-spacing-vertical: 1rem;
  --piko-block-spacing-vertical: calc(var(--piko-spacing) * 2);
  --piko-block-spacing-horizontal: var(--piko-spacing);
  --piko-form-element-spacing-vertical: 0.75rem;
  --piko-form-element-spacing-horizontal: 1rem;
  --piko-group-box-shadow: 0 0 0 rgba(0, 0, 0, 0);
