  --piko-group-box-shadow-focus-with-input: 0 0 0 0.0625rem var(--piko-form-element-border-color);
  --piko-modal-overlay-backdrop-filter: blur(0.375rem);
  --piko-nav-element-spacing-vertical: 1rem;
  --piko-nav-element-spacing-horizontal: 0.5rem;
