@media (min-width: 576px) {
  :root {
    --piko-font-size: 106.25%;
  }
}
@media (min-width: 768px) {
  :root {
    --piko-font-size: 112.5%;
  }
}
@media (min-width: 1024px) {
  :root {
    --piko-font-size: 118.75%;
  }
}
@media (min-width: 1280px) {
  :root {
    --piko-font-size: 125%;
  }
}
@media (min-width: 1536px) {
  :root {
    --piko-font-size: 131.25%;
  }
}
:root details summary[role=button]::after {
  filter: brightness(0) invert(1);
}
:root [aria-busy=true]:not(input, select, textarea):is(button, [type=submit], [type=button], [type=reset], [role=button])::before {
  filter: brightness(0) invert(1);
}

@media (min-width: 576px) {
  body > header,
  body > main,
  body > footer,
  section {
    --piko-block-spacing-vertical: calc(var(--piko-spacing) * 2.5);
  }
}
@media (min-width: 768px) {
  body > header,
  body > main,
  body > footer,
  section {
    --piko-block-spacing-vertical: calc(var(--piko-spacing) * 3);
  }
}
@media (min-width: 1024px) {
  body > header,
  body > main,
  body > footer,
  section {
    --piko-block-spacing-vertical: calc(var(--piko-spacing) * 3.5);
  }
}
@media (min-width: 1280px) {
  body > header,
  body > main,
  body > footer,
  section {
    --piko-block-spacing-vertical: calc(var(--piko-spacing) * 4);
  }
}
@media (min-width: 1536px) {
  body > header,
  body > main,
  body > footer,
  section {
