  }
}

@media (min-width: 576px) {
  article {
    --piko-block-spacing-horizontal: calc(var(--piko-spacing) * 1.25);
  }
}
@media (min-width: 768px) {
  article {
    --piko-block-spacing-horizontal: calc(var(--piko-spacing) * 1.5);
  }
}
@media (min-width: 1024px) {
  article {
    --piko-block-spacing-horizontal: calc(var(--piko-spacing) * 1.75);
  }
}
@media (min-width: 1280px) {
  article {
    --piko-block-spacing-horizontal: calc(var(--piko-spacing) * 2);
  }
}
@media (min-width: 1536px) {
  article {
    --piko-block-spacing-horizontal: calc(var(--piko-spacing) * 2.25);
  }
}

dialog > article {
  --piko-block-spacing-vertical: calc(var(--piko-spacing) * 2);
  --piko-block-spacing-horizontal: var(--piko-spacing);
}
@media (min-width: 576px) {
  dialog > article {
    --piko-block-spacing-vertical: calc(var(--piko-spacing) * 2.5);
    --piko-block-spacing-horizontal: calc(var(--piko-spacing) * 1.25);
  }
}
@media (min-width: 768px) {
  dialog > article {
    --piko-block-spacing-vertical: calc(var(--piko-spacing) * 3);
    --piko-block-spacing-horizontal: calc(var(--piko-spacing) * 1.5);
  }
}

a {
  --piko-text-decoration: underline;
}
a.secondary, a.contrast {
  --piko-text-decoration: underline;
}

small {
