}

h1,
h2,
h3,
h4,
h5,
h6 {
  --piko-font-weight: 700;
}

h1 {
  --piko-font-size: 2rem;
  --piko-line-height: 1.25;
  --piko-typography-spacing-top: 3rem;
}

h2 {
  --piko-font-size: 1.75rem;
  --piko-line-height: 1.3;
  --piko-typography-spacing-top: 2.625rem;
}

h3 {
  --piko-font-size: 1.5rem;
  --piko-line-height: 1.35;
  --piko-typography-spacing-top: 2.25rem;
}

h4 {
  --piko-font-size: 1.25rem;
  --piko-line-height: 1.4;
