}

h5 {
  --piko-font-size: 1.125rem;
  --piko-line-height: 1.45;
  --piko-typography-spacing-top: 1.6875rem;
}

h6 {
  --piko-font-size: 1rem;
  --piko-typography-spacing-top: 1.5rem;
}

thead th,
thead td,
tfoot th,
tfoot td {
  --piko-font-weight: 600;
  --piko-border-width: 0.1875rem;
}

pre,
code,
kbd,
samp {
  --piko-font-family: var(--piko-font-family-monospace);
}

kbd {
  --piko-font-weight: bolder;
}

input:not([type=submit],
[type=button],
[type=reset],
[type=checkbox],
[type=radio],
[type=file]),
:where(select, textarea) {
  --piko-outline-width: 0.0625rem;
}

[type=search] {
  --piko-border-radius: 5rem;
}

[type=checkbox],
[type=radio] {
  --piko-border-width: 0.125rem;
}

[type=checkbox][role=switch] {
  --piko-border-width: 0.1875rem;
}

[role=search] {
  --piko-border-radius: 5rem;
}

[role=search] button,
[role=search] [type=submit],
[role=search] [type=button],
[role=search] [role=button],
[role=group] button,
[role=group] [type=submit],
[role=group] [type=button],
[role=group] [role=button] {
  --piko-form-element-spacing-horizontal: 2rem;
}

